@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    font-family:
        -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family:
        source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

@layer base {
    :root {
        --background: 0 0% 100%;
        --foreground: 0 0% 3.9%;
        --card: 0 0% 100%;
        --card-foreground: 0 0% 3.9%;
        --popover: 0 0% 100%;
        --popover-foreground: 0 0% 3.9%;
        --primary: 0 0% 9%;
        --primary-foreground: 0 0% 98%;
        --secondary: 0 0% 96.1%;
        --secondary-foreground: 0 0% 9%;
        --muted: 0 0% 96.1%;
        --muted-foreground: 0 0% 45.1%;
        --accent: 0 0% 96.1%;
        --accent-foreground: 0 0% 9%;
        --destructive: 0 84.2% 60.2%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 89.8%;
        --input: 0 0% 89.8%;
        --ring: 0 0% 3.9%;
        --chart-1: 12 76% 61%;
        --chart-2: 173 58% 39%;
        --chart-3: 197 37% 24%;
        --chart-4: 43 74% 66%;
        --chart-5: 27 87% 67%;
        --radius: 0.5rem;
    }
    .dark {
        --background: 0 0% 3.9%;
        --foreground: 0 0% 98%;
        --card: 0 0% 3.9%;
        --card-foreground: 0 0% 98%;
        --popover: 0 0% 3.9%;
        --popover-foreground: 0 0% 98%;
        --primary: 0 0% 98%;
        --primary-foreground: 0 0% 9%;
        --secondary: 0 0% 14.9%;
        --secondary-foreground: 0 0% 98%;
        --muted: 0 0% 14.9%;
        --muted-foreground: 0 0% 63.9%;
        --accent: 0 0% 14.9%;
        --accent-foreground: 0 0% 98%;
        --destructive: 0 62.8% 30.6%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 14.9%;
        --input: 0 0% 14.9%;
        --ring: 0 0% 83.1%;
        --chart-1: 220 70% 50%;
        --chart-2: 160 60% 45%;
        --chart-3: 30 80% 55%;
        --chart-4: 280 65% 60%;
        --chart-5: 340 75% 55%;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    body {
        @apply bg-background text-foreground;
    }
}

/* Prevent horizontal scroll caused by overflowing elements */
html, body {
    overflow-x: hidden;
}

/* Hide scrollbars for clean carousels and scroll areas */
.no-scrollbar {
    -ms-overflow-style: none; /* Internet Explorer 10+ */
    scrollbar-width: none; /* Firefox */
}
.no-scrollbar::-webkit-scrollbar {
    display: none; /* Safari and Chrome */
}

/* Fluid typography utilities using clamp():
   - hero-xl: large hero title
   - hero-sub: hero subtitle/lead paragraph
   - section-title: section headings (h3)
   These scale between a min and max size based on viewport width for better readability on mobile. */
.text-hero-xl {
    font-size: clamp(1.8rem, 4.5vw, 3.75rem); /* ~28px -> 60px */
    line-height: 1.03;
}
.text-hero-sub {
    font-size: clamp(0.95rem, 2.8vw, 1.125rem); /* ~15px -> 18px */
    line-height: 1.4;
    color: var(--muted-foreground, #9ca3af);
}
.text-section-title {
    font-size: clamp(1.125rem, 2.4vw, 1.75rem); /* ~18px -> 28px */
    line-height: 1.2;
}

/* Responsive button utility for category buttons in the hero.
   Uses clamp to scale font-size and adjusts padding for small screens. */
.btn-responsive {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: clamp(0.875rem, 2.2vw, 1rem); /* ~14px -> 16px */
    padding: clamp(0.375rem, 0.9vw, 0.5rem) clamp(0.75rem, 2.2vw, 1rem);
    border-radius: 0.5rem;
    transition: transform 120ms ease, box-shadow 120ms ease;
}
.btn-responsive:active {
    transform: translateY(1px) scale(0.995);
}
.btn-responsive:focus {
    outline: 2px solid rgba(59,130,246,0.35);
    outline-offset: 2px;
}

/* Ensure carousel items keep their width and don't stretch vertically */
.carousel-item {
    flex: 0 0 auto; /* keep intrinsic min-width set on wrappers */
    display: flex;
    align-items: flex-start; /* align card top edges */
}

/* Ensure Tailwind container doesn't exceed viewport on small screens */
.container {
    max-width: 100%;
    box-sizing: border-box;
    padding-left: 1rem; /* px-4 equivalent */
    padding-right: 1rem;
}

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }

    [data-debug-wrapper="true"] > * {
        margin-left: inherit;
        margin-right: inherit;
        margin-top: inherit;
        margin-bottom: inherit;
        padding-left: inherit;
        padding-right: inherit;
        padding-top: inherit;
        padding-bottom: inherit;
        column-gap: inherit;
        row-gap: inherit;
        gap: inherit;
        border-left-width: inherit;
        border-right-width: inherit;
        border-top-width: inherit;
        border-bottom-width: inherit;
        border-left-style: inherit;
        border-right-style: inherit;
        border-top-style: inherit;
        border-bottom-style: inherit;
        border-left-color: inherit;
        border-right-color: inherit;
        border-top-color: inherit;
        border-bottom-color: inherit;
    }
}
